<template>
    <!-- = -->
  <div class="process-log">
    <el-table
      :data="tableData"
      height="100%"
      border
      style="width: 100%">
      <el-table-column
        prop="date"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="log"
        :show-overflow-tooltip="true"
        label="">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
    export default {
        name: "processLog",
        data(){
          return {
            tableData:[{
              date:'2019-08-10 12:40:12',
              log:''
            }]
          }
        },
    }
</script>

<style scoped>
.process-log{
  height: 100%;
}
</style>



