<template>
    <!-- = -->
    <div class="operational-records-wrap">
      <el-table
        :data="tableData"
        height="100%"
        border
        style="width: 100%">
        <el-table-column
          prop="date"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="personnel"
          label="">
        </el-table-column>
        <el-table-column
          prop="operation"
          label="">
        </el-table-column>
        <el-table-column
          prop="describe"
          label="">
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
    export default {
        name: "operationalRecords",
        data(){
          return {
            tableData:[{
              date:'2019-08-10 12:40:12',
              personnel:'',
              operation:'',
              describe:'',
            }]
          }
        }
    }
</script>

<style scoped lang="less">
.operational-records-wrap{
  height: 100%;
}
</style>



